<template>
  <div id="style-operate">
    <!-- Form -->
    <el-dialog v-model="dialogFormVisible" :before-close="beforeClose" :title="titleName" width="600" draggable>
      <!-- Form 自定义表单 -->
      <el-form :model="form" :rules="rules" ref="ruleForm" label-width="80px">
        <el-form-item :label="$t('MessageView.title')" prop="title">
          <el-input v-model="form.title" :placeholder="$t('MessageView.titlePlaceholder')" :readonly="isReadonly"></el-input>
        </el-form-item>
        <el-form-item :label="$t('MessageView.titleEn')" prop="titleEn">
          <el-input v-model="form.titleEn" :placeholder="$t('MessageView.titleEnPlaceholder')" :readonly="isReadonly"></el-input>
        </el-form-item>

        <el-form-item :label="$t('MessageView.type')" prop="type">
          <el-select v-model="form.type" :placeholder="$t('MessageView.typePlaceholder')" :disabled="isReadonly">
            <el-option :label="$t('pullDown.messageType.complaint')" :value=1></el-option>
            <el-option :label="$t('pullDown.messageType.system')" :value=2></el-option>
          </el-select>
        </el-form-item>
        <el-form-item :label="$t('MessageView.pushName')" prop="pushTime">
          <el-select v-model="form.pushTime" :placeholder="$t('MessageView.pushNamePlaceholder')" :disabled="isReadonly">
            <el-option :label="$t('pullDown.messagePushTime.goTime')" :value=1></el-option>
            <el-option :label="$t('pullDown.messagePushTime.goPlus')" :value=2></el-option>
          </el-select>
        </el-form-item>
        <el-form-item :label="$t('MessageView.goTime')" required v-if="form.pushTime == 1">
          <el-col :span="11">
            <el-form-item prop="startDate">
              <el-date-picker type="date" :placeholder="$t('MessageView.startDate')" v-model="form.startDate" format="YYYY-MM-DD" value-format="YYYY-MM-DD" style="width: 100%;"></el-date-picker>
            </el-form-item>
          </el-col>
          <el-col :span="2" class="text-center"><el-icon><Minus /></el-icon></el-col>
          <el-col :span="11">
            <el-form-item prop="endData">
              <el-date-picker type="date" :placeholder="$t('MessageView.endDate')" v-model="form.endData" format="YYYY-MM-DD" value-format="YYYY-MM-DD" style="width: 100%;"></el-date-picker>
            </el-form-item>
          </el-col>
        </el-form-item>

        <el-form-item :label="$t('MessageView.content')" prop="content">
          <el-input type="textarea" v-model="form.content" :placeholder="$t('MessageView.contentPlaceholder')" :readonly="isReadonly"></el-input>
        </el-form-item>
        <el-form-item :label="$t('MessageView.contentEn')" prop="contentEn">
          <el-input type="textarea" v-model="form.contentEn" :placeholder="$t('MessageView.contentEnPlaceholder')" :readonly="isReadonly"></el-input>
        </el-form-item>
      </el-form>

      <template #footer>
      <span class="dialog-footer">
        <!-- Form 按钮 -->
        <el-button @click="closeButton()">
          {{ $t('common.button.cancel') }}
        </el-button>
        <el-button type="primary" v-show="type != 'detail'" @click="submitButton()">
          {{ $t('common.button.confirm') }}
        </el-button>
      </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import {ElNotification} from "element-plus";
import {saveMessagePush, updateMessagePush} from "@/http/clientPage/messageApi";

export default {
  name: "style-operate",
  // data: 对象就是要渲染到页面上的数据
  data: function () {
    return {
      // 控制对话框大开闭
      dialogFormVisible: false,
      // 联动列表数据 - 行数据副本
      rowData: {},
      form: {},
      // 表单验证规则
      rules: {
        title: [
          {required: true, message: this.$t('MessageView.titlePlaceholder'), trigger: 'blur'},
        ],
        titleEn: [
          {required: true, message: this.$t('MessageView.titleEnPlaceholder'), trigger: 'blur'},
        ],
        type: [
          {required: true, message: this.$t('MessageView.typePlaceholder'), trigger: 'change'},
        ],
        pushTime: [
          {required: true, message: this.$t('MessageView.pushNamePlaceholder'), trigger: 'change'},
        ],

        startDate: [
          {required: true, message: this.$t('MessageView.startDate'), trigger: 'blur'},
        ],
        endData: [
          {required: true, message: this.$t('MessageView.endDate'), trigger: 'blur'},
        ],

        content: [
          {required: true, message: this.$t('MessageView.contentPlaceholder'), trigger: 'blur'},
        ],
        contentEn: [
          {required: true, message: this.$t('MessageView.contentEnPlaceholder'), trigger: 'blur'},
        ],
      },
      // 进入类型: 新增 详情 编辑
      type: '',
      titleName: this.$t('common.dialog.defaultTitle'),
      // 表单的只读
      isReadonly: false,
      formLabelWidth: '150px'
    }
  },

  // watch 侦听器 能够侦听到data数据的变化 且获取对应的 新值 和 旧值
  watch: {
    type(newVal, oldVal) {
      if (newVal == '') return
      if (newVal == 'add') this.titleName = this.$t('common.dialog.addTitle')
      if (newVal == 'edit') this.titleName = this.$t('common.dialog.editTitle')
      if (newVal == 'detail') this.titleName = this.$t('common.dialog.detailTitle')
    }
  },

  // moubted: 页面加载完毕后执行
  mounted() {

  },

  // methods: 对象中的方法，可以在页面中调用
  methods: {

    // 点击遮罩和x时执行
    beforeClose(done) {
      this.closeButton();
      done(); // 必须调用 done，以继续关闭对话框
    },
    // 取消按钮
    closeButton() {
      this.dialogFormVisible= false;
      this.isReadonly= false;
      this.form= {};
      this.rowData= {};
      this.$refs.ruleForm.resetFields(); // 重置表单验证
    },
    // 确认按钮
    submitButton() {
      this.$refs.ruleForm.validate((valid) => {
        if (valid) {
          if (this.type == "add") {
            saveMessagePush(this.form).then(res => {
              if (res.data.status == 200) {
                this.dialogFormVisible= false;
                ElNotification({title: this.$t('common.message.successTitle'), message: res.data.msg, type: 'success',})
                // 刷新列表
                this.$emit('operateClick');
              }
            }).catch((erro) => { });
          } else if (this.type == "edit") {
            updateMessagePush(this.form).then(res => {
              if (res.data.status == 200) {
                this.dialogFormVisible= false;
                ElNotification({title: this.$t('common.message.successTitle'), message: res.data.msg, type: 'success',})
                // 刷新列表
                this.$emit('operateClick');
              }
            }).catch((erro) => { });
          }
          this.closeButton();
        } else {
          return false;
        }
      });
    }
  },
}
</script>

<!--组件css样式 scoped:表示该样式只在此组件生效-->
<style scoped>
.el-button--text {
  margin-right: 15px;
}
.el-select {
  width: 300px;
}
.el-input {
  width: 300px;
}
.dialog-footer button:first-child {
  margin-right: 10px;
}
.text-center {
  text-align: center;
}
</style>
